<template>
	<view class="indexcontainer">
		<swiper class="banners" 
		   	indicator-dots 	
		    autoplay
		    interval="2000"
		    circular
		    indicator-active-color="aqua"
		     indicator-color="#333">
		   <swiper-item>
		     <image src="http://lwandzxl.love/img/1.jpg"></image>
		   </swiper-item>
		    <swiper-item>
		     <image src="http://lwandzxl.love/img/2.jpg"></image>
		   </swiper-item>
		    <swiper-item>
		     <image src="http://lwandzxl.love/img/3.jpg"></image>
		   </swiper-item>
		    <swiper-item>
		     <image src="http://lwandzxl.love/img/4.jpg"></image>
		   </swiper-item>
		    <swiper-item>
		     <image src="http://lwandzxl.love/img/5.jpg"></image>
		   </swiper-item>
		   </swiper>
		   <view class="recommendContainer"> 
		   <NavHeader title="热门文章" nav="精心为您推荐"></NavHeader>
		    <scroll-view class="recommendScroll" enable-flex scroll-x>
		     <view @click="toarticleinfo(item.id)" class="scrollItem" v-for="(item,index) in articlelist"  :key="index">
		     <image :src="item.cover"></image>
		       <text>{{item.title}}</text>
		     </view>
		     </scroll-view>  
		   </view>
		   <view class="recommendContainer">
		   <NavHeader title="郎小宝music" nav="为您专属定制"></NavHeader>
		    <scroll-view class="recommendScroll" enable-flex scroll-x>
		     <view @click="tomusicinfo(item.id)" class="scrollItem" v-for="(item,index) in musiclist"  :key="index">
		     <image :src="item.pic"></image>
		       <text>{{item.title}}</text>
		     </view>
		     </scroll-view>  
		   </view>
		   <view class="recommendContainer">
		   <NavHeader title="最新视频" nav="最新推荐视频"></NavHeader>
		    <scroll-view class="recommendScroll" enable-flex scroll-x>
		     <view @click="tovideoinfo(item.id)" class="scrollItem" v-for="(item,index) in videolist"  :key="index">
		     <image :src="item.cover"></image>
		       <text>{{item.title}}</text>
		     </view>
		     </scroll-view>  
		   </view>
	</view>
</template>

<script>
import { mapState } from "vuex";
	export default {
	  data() {
	  	return {
	  		articlelist:[],
			musiclist:[],
			videolist:[]
	  	}
	  },
	  computed:{
		    ...mapState("loginModule", ["userinfo"]),
	  },
	  methods:{
		  async getvideo(page) {
		   let res =await  this.$zxl('/getvideo',{ page })
		       if (res.data.status === 200) {
				   // console.log(res.data.data)
		  		this.videolist = res.data.data.splice(0,30);
	
		    
		       }
		   },
		  onNavigationBarButtonTap(e){
		  	console.log(e)
			if(e.index==0){
				uni.reLaunch({
					url:'/pages/personal/personal'
				})
			}
			else{
				uni.navigateTo({
					url:"/pages/Liulanjilu/Liulanjilu"
				})
			}
		  },
		  onNavigationBarSearchInputClicked(){
			uni.navigateTo({
				url:"/pages/search/search"
			})
		  },
		  toarticleinfo(id){
			  uni.navigateTo({
			  	url:`/pages/ArticleInfo/ArticleInfo?id=${id}`
			  })
		  },
		  tomusicinfo(id){
			  console.log(id)
		  	 uni.navigateTo({
			url:`/pages/musicintroduction/musicintroduction?musicId=${id}`
		 })
		  },
		  tovideoinfo(id){
		  			  console.log(id)
		   	 uni.navigateTo({
		  			// url:`/pages/musicintroduction/musicintroduction?musicId=${id}`
		  			url:`/pages/VideoInfo/VideoInfo?id=${id}`
		  })
		   },
	async getarticle(){
		let zxl=await this.$zxl('/gethotlist',{page:1})
		// console.log(zxl)
		this.articlelist=zxl.data.data
	},
	async getmusic(){
		let zxl=await this.$zxl('/getmusic',{page:1})
		// console.log(zxl)
		this.musiclist=zxl.data.data
	}
	  },
	  onLoad() {
	  	this.getarticle()
		this.getmusic()
		this.getvideo()
		console.log(this.userinfo,'useinfo')
	  }
	}
</script>

<style lang="scss" scoped>
/* pages/insex/index.wxss */
.indexcontainer{
   width: 100%;
  height: 300rpx;
}
.banners image{
width: 100%;
height:100%;
}
.navcontainer{
  display: flex;

}
.navitem{
   display: flex;
   flex-direction: column;
   text-align: center;
   width: 20%;
}
.navitem .iconfont {
  width: 100rpx;
  height: 100rpx;
  border-radius: 50%;
  text-align: center;
  line-height: 100rpx;
  background: rgb(240, 19, 19);
  font-size: 50rpx;
  color: #fff;
  margin: 20rpx 20rpx;
}
.navitem text {
  font-size: 26rpx;
}

.recommendContainer{
  padding:5rpx 20rpx;
  margin-bottom: 20rpx;
  // background-color: #999;
   background: -webkit-linear-gradient(top, pink, #999999);
}


.scrollItem{
width: 200rpx;
display:inline-block;
margin-right: 20rpx;
}
.scrollItem image{
  width: 200rpx;
  height: 200rpx;
  border-radius: 10rpx;
}
.recommendScroll{
  display: flex;
  white-space: nowrap;
  // height: 280rpx;
  height: 280rpx;
}
.scrollItem text{
  font-size: 26rpx;
  /* 单行文本溢出隐藏 省略号代替 */
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  /* 多行文本溢出隐藏 省略号代替*/
  // overflow: hidden;
  // text-overflow: ellipsis;
  // display: -webkit-box;
  // -webkit-box-orient: vertical; /*设置对其模式*/
  // -webkit-line-clamp: 2; /*设置多行的行数*/
}
/* 排行榜 */
.topList{
  padding: 20rpx;
  margin-top: -15rpx;
}
.topListSwiper {
  height: 400rpx;
}

.swiperItem {
  width: 96%;
  background: #fbfbfb;
}
.swiperItem .title {
  font-size: 30rpx;
  line-height: 80rpx;

}

.musicItem {
  /* 当一个元素设置为flex，其子元素会自动成为block元素*/
  display: flex;
  margin-bottom: 20rpx;
}

.musicItem image{
  width: 100rpx;
  height: 100rpx;
  border-radius: 6rpx;
}

.musicItem .count {
  width: 100rpx;
  height: 100rpx;
  text-align: center;
  line-height: 100rpx;
}

.musicItem .musicName {
  height: 100rpx;
  line-height: 100rpx;
  max-width: 400rpx;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
	
	
